

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"),
    local("Montserrat-Regular"),
    url("../fonts/montserrat-v14-cyrillic_latin-regular.woff2") format("woff2"),
    url("../fonts/montserrat-v14-cyrillic_latin-regular.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat SemiBold"),
    local("Montserrat-SemiBold"),
    url("../fonts/montserrat-v14-cyrillic_latin-600.woff2") format("woff2"),
    url("../fonts/montserrat-v14-cyrillic_latin-600.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Montserrat Medium"),
    local("Montserrat-Medium"),
    url("../fonts/montserrat-v14-latin_cyrillic-500.woff2") format("woff2"),
    url("../fonts/montserrat-v14-latin_cyrillic-500.woff") format("woff");
}

/* montserrat-700 - latin_cyrillic */
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat Bold"),
    local("Montserrat-Bold"),
    url("../fonts/montserrat-v14-latin_cyrillic-700.woff2") format("woff2"),
    url("../fonts/montserrat-v14-latin_cyrillic-700.woff") format("woff");
}
